html,
body {
  overflow: hidden;
  height: 100%;
}
.side {
  box-sizing: border-box;
  overflow: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 600px;
  padding: 15px;
  background: #f1f1f1;
}
.main {
  position: absolute;
  top: 0;
  left: 600px;
  right: 0;
  bottom: 0;
}
.main iframe {
  width: 100%;
  height: 100%;
  background: #fcfcfc;
  border: 0;
}
